<script src="https://code.jquery.com/jquery-3.6.0.min.js"></script>

<script type="text/javascript">
import $ from 'jquery';
import axios from 'axios';
import JSZip from 'jszip';
const featureMap = new Map(); // 创建特征编号到特征名称的映射
const filePath = "./json/Dimacs.txt";
const outputArray = []; // 用于存储输出的数组
let groupIndex = 0; // 用于跟踪组的索引
let cnt = 0;
let num = 0;
let cnf = 0;
const file_Path = './json/ecos.txt'; // 替换为您的文件路径import JSZip from 'jszip';
import Home from '../views/Home.vue'
  export default {
    name: "Execute",
    data() {
        return {

          num:0,
          cnf:0,

          file_isDropdownVisible:false,
          execute_isDropdownVisible:false,
          configuration_isDropdownVisible:false,
          help_isDropdownVisible:false,
          generationTechniqueDropdownVisible: false,
          prioritizationTechniqueDropdownVisible: false,

          upload_model:false,
          upload_product:false,
          upload_product_after_model:false,

          load_model_Visible:false,
          load_product_Visible:false,
          load_product_after_model_Visible:false,
          generate_product_Visible:false,

          bottom_no_file_loaded_Visible:true,
          bottom_loading_model:false,
          bottom_loading_model_dir:false,
          bottom_loading_product:false,
          bottom_loading_product_after_model:false,
          bottom_generating_product_Visible:false,
          bottom_completed:false,

          informationElement:[],
          featureElement:[],
          constraintElement:[],
          productElement:[],
          coverageElement:[],
          excute_language : this.$store.state.E_language,
          
          outputArray: [],

          feature_Numbers: [], // 初始化为空数组
          feature_Names: [], // 初始化为空数组
          constraints_data: [], // 初始化为空数组
        }
    },
    mounted() {
      // this.processFeatureFile(filePath);
      // this.processEcosFile(file_Path);
    },
    created() {
    const jsonFile = "./json/data.json";
    axios.get(jsonFile)
      .then(response => {
        // this.informationElement = response.data.Feature_Model_Information;
        // this.featureElement = response.data.Features;
        // this.constraintElement = response.data.CNF_Constraints;
        // this.productElement = response.data.Products;
        // this.coverageElement = response.data.Pairwise_Coverage;
        // 添加其他数据部分的赋值
      })
      .catch(error => {
        console.error('加载数据失败：', error);
      });
    },
    methods: {
      processEcosFile() {
        var fileContent = ''
        var file = this.$store.state.fms
        console.log(file);
        const reader = new FileReader();

        reader.onload = (event) => {
          fileContent = event.target.result; // 文件内容存储在 result 属性中
          console.log(fileContent); // 输出文件内容
          console.log(111);
          // 在这里可以对文件内容进行进一步的操作
        };

        reader.readAsText(file); // 以文本形式读取文件内容

      fetch(file_Path)
        .then(response => response.text())
        .then(data => {
          num=0;
          cnf=0;
          // const lines = data.split('\n');
          const lines = fileContent.split('\n');

          const feature_Map = new Map(); // 创建特征编号到特征名称的映射
          const constraints = [];

          lines.forEach(line => {
            if (line.startsWith('c ')) {
              num++;
              // 行以 'c ' 开头，是特征行。
              const parts = line.split(' ');
              const feature_Number = parseInt(parts[1]);
              const feature_Name = parts.slice(2).join(' ');
              feature_Map.set(feature_Number, feature_Name);
              // 在循环内部将每个特征的编号和名称添加到数组
              this.feature_Numbers.push(feature_Number);
              this.feature_Names.push(feature_Name);
            } else if (line.indexOf("cnf") === -1 && line.trim() !== "") {
              cnf++;
              // 行以数字+空格+0结尾，是 CNF 行。
              const cnfParts = line.split(' ');
              const cnfClauses = cnfParts.map(part => parseInt(part));
              const validClauses = cnfClauses.filter(clause => clause !== 0 && clause !== -0); // 排除序号为0的情况
              const cnfNames = validClauses.map(clause => {
                if (clause > 0) {
                  return feature_Map.get(clause);
                } else {
                  return `!${feature_Map.get(Math.abs(clause))}`;
                }
              });
              const cnfLine = cnfNames.join(' OR ');
              constraints.push(cnfLine);
            }
          });

          // 设置 CNF 约束数组
          this.constraints_data = constraints;
          this.cnf=cnf;
          this.num=num;
        })
        .catch(error => {
          console.error('读取文件时发生错误:', error);
        });
    },
      processFeatureFile(filePath) {
        var product = this.$store.state.productsStr
        fetch(filePath)
          .then(response => response.text())
          .then(data => {
            // const lines = data.split('\n');
            const lines = product.split('\n');

            lines.forEach(line => {
              if (!line.includes('->')) {
                groupIndex++;
                this.processLine(line, groupIndex); // 处理每一行，并传递组索引
              }
            });

      // 打印整个输出数组
      outputArray.forEach(output => {
        //console.log(output);
          });
        })
        .catch(error => {
          console.error('Error reading the file:', error);
        });
    },

      processLine(line, groupIndex) {
          cnt++;

          const selectedFeatures = line.split(';').map((item) => parseInt(item));

          // 使用 filter 过滤掉 NaN（空白）项和负数项
          const validSelectedFeatures = selectedFeatures.filter(featureNumber => !isNaN(featureNumber));

          // 根据映射查找特征名称
          // const selectedFeatureNames = validSelectedFeatures.map(featureNumber => {
          //   return featureMap.get(featureNumber);
          // });
          // 生成带标签的输出
          
          const labeledOutput = [`${groupIndex === 0 ? "P"+(cnt-1)+":" : `P${groupIndex}:`}`].concat(validSelectedFeatures);

          // 在这里可以处理带标签的输出
          //console.log('Selected Features:', labeledOutput);

          // 将带标签的输出添加到输出数组
          this.outputArray.push(labeledOutput);
          //console.log('outputArray:', outputArray);
        //console.log(outputArray);
      },
          // 触发文件选择器的点击事件
