                console.log('1载入文件'+file);
                console.log('2输出文件'+this.$store.state.fms);
              }else{
                alert("模型载入失败")
              }

              if(this.upload_model === true)
              {
                this.load_model_activate();
              }
              else if(this.upload_product === true)
              {
                this.load_product_activate();
              }
              else if(this.upload_product_after_model === true)
              {
                this.load_product_after_model_activate();
              }
              
              this.processEcosFile();
          
