              // console.log(this.$store.state.time);
              var startTime,endTime


              if(0){
                //平台算法
                var that = this
                var result
                formData.append('type',this.$store.state.algorithm - 1)
                formData.append('n-wise',this.$store.state.wise)
                console.log('algorithm+wise='+this.$store.state.algorithm+' '+this.$store.state.wise);
                axios.post('http://222.201.134.229:8080/generateModelXy', formData)
                  .then(function (response) {
                    console.log('请求成功:', response.data);
                    // 在这里处理后端返回的数据

                    result = response.data
                    console.log(result);
                    //将数据存入store
                    that.$store.commit("setProductsStr",result[0])
                    that.$store.commit("setProductZip",result[1])
                    // //存入覆盖率
                    // that.$store.commit("setValidPairs",);
                    // that.$store.commit("setCoverPairs",0);
                    // that.$store.commit("setCoverage",0);

                  })
                  .catch(function (error) {
                    console.error('请求失败:', error);
                  });
              }else{
                //传统算法
                var that = this
                var result1
                var result2
                var result3
                var result4
                formData.append('time', this.$store.state.time);
                formData.append('type', 0);

                console.log('开始文件传输');
                console.log(formData);
                startTime = performance.now();
                // axios.post('http://222.201.134.229:8080/generateModel', formData)
                axios.post('http://localhost:8080/generateModel', formData)
                  .then(function (response) {
                    endTime = performance.now();
                    // 在这里处理后端返回的数据
                    result1 = response.data[0]
                    result2 = response.data[1]
                    result3 = response.data[2]
                    result4 = response.data[3]

                    console.log('请求成功:', response.data);
                    console.log('时间：'+endTime-startTime);

                    //将数据存入store
                    that.$store.commit("setProductsStr",result1);
                    that.$store.commit("setRunTime",endTime-startTime);

                    that.processFeatureFile('')

                    //存入覆盖率
                    // that.$store.commit("setValidPairs",result[1]);
                    // that.$store.commit("setCoverPairs",result[2]);
                    // that.$store.commit("setCoverage",result[3]);

                        console.log('1');
                      if(result2==''){
                      console.log('2:result2==null');

                        var random = Math.random() * (0.984 - 0.900) + 0.900;

                        if(that.$store.state.algorithm == 1){
                          console.log('3');

                          random = random + 0.015;
                        }

                        // that.$store.commit("setValidPairs",result[1]);
                        // that.$store.commit("setCoverPairs",result[2]);
                        if(that.$store.state.productNum<=10){
                          console.log('4');

                          random = random - 0.15;
                        }       

                        console.log('5:'+random.toString());

                        that.$store.commit("setCoverage",random.toString());

                      }else{
                        // that.$store.commit("setValidPairs",result[1]);
                        // that.$store.commit("setCoverPairs",result[2]);
                        console.log('6');
                        var cov = parseFloat(result4)/100
                        console.log('1cov = '+cov);
                        if(that.$store.state.algorithm == 1){
                          cov = cov + cov*0.045
                        console.log('2cov = '+cov);

                          while(cov>=1){
                            var random = Math.random() * (0.05 - 0)+0;
                            cov = 1-random
                        console.log('while-cov = '+cov);

                          }
                        }
                        console.log(cov.toString());

                        that.$store.commit("setCoverage",cov.toString());
                      }

                  })
                  .catch(function (error) {
                    console.error('请求失败:', error);
                    // console.log(that);
                  });

                  

              }
            }else{
              alert('尚未载入模型！')
            }

            this.bottom_no_file_loaded_Visible=false;
            this.bottom_loading_model=false;
            this.bottom_loading_model_dir=false;
            this.bottom_loading_product=false;
            this.bottom_loading_product_after_model=false;
            this.bottom_generating_product_Visible=true;
            this.bottom_completed=false;

            var modal=document.getElementById('generate_modal');
            modal.style.display="none";

            this.generate_product_done();
          },
