.load_feature_model, .load_product, .load_product_after_model, .quit {
  flex-direction: row;
  display: flex;
  align-items: center;
  padding: 5px; // 加入padding以保证hover效果更流畅
  &:hover { 
    background-color: #ddd; // 当hover到整个容器时改变背景色
    span { 
      color: #807e7e; 
    }
  }
  span, li {
    display: inline-block;
    margin: 0; 
    border: none;
  }
  li {
    width: 30px;
    height: 30px;
    border: none;
    display: flex;
    margin: 0 5px; // 改变此处的margin可以调整图标和文字之间的空隙

    justify-content: center;
    align-items: center;
    img {
      width: 65%;
      height: 65%;
    }
  }
}


