<template>
    <div>
        <div class="header">
            <div class="box">
                <h1>
                    <img src="../assets/bg.png">
                    <!-- <span>软件产品线</span> -->
                </h1>
                <nav>
                    <router-link active-class="active" to="/home"> {{this.$store.state.language[18] }} </router-link>
                    <router-link active-class="active" to="/execute"> {{this.$store.state.language[19] }} </router-link>
                    <router-link active-class="active" to="/algorithm"> {{this.$store.state.language[20] }} </router-link>
                </nav>
            </div>
            <div class="box">
                <nav>
                    <!-- <a >消息</a> -->
                    <!-- <a>关于我们</a> -->
                    <div class="language-dropdown-wrapper">
                        <span active-class="active" @click="toggleDropdown()"> {{this.$store.state.language[21] }} </span>
                        <div class="dropdown" v-if="isDropdownOpen">
                            <div class="dropdown-item">
                                <input type="radio" id="chinese" value="1" v-model="language">
                                <label for="chinese">中文</label>
                            </div>
                            <div class="dropdown-item">
                                <input type="radio" id="english" value="2" v-model="language">
                                <label for="english">English</label>
                            </div>
                        </div>
                    </div>
                    <router-link active-class="active" to="/about"> {{this.$store.state.language[22] }} </router-link>
                </nav>
            </div>
        </div>
        <div class="contents">
            <!--指定组件的位置-->
            <router-view></router-view> 
        </div>
       
    </div>
    
</template>

<script>
export default {
    name: "Head",
    data() {
        return {
            isDropdownOpen: false, // Track dropdown state
            language: null,    // Track selected language
           
        };
    },
    watch:{
        language(){
             console.log(this.$store.state.language_state);
             this.$store.state.language_state = this.language;
             console.log(this.$store.state.language_state);
             this.$store.commit('changeLanguage', this.$store.state.language_state);
             console.log(this.$store.state.language);
             
             
        }
    },
    computed: {
        isAlgorithmActive() {
            return this.$route.name === 'about';
        }
    },
    methods: {
        toggleDropdown() {
            this.isDropdownOpen = !this.isDropdownOpen;
        },
        refreshPage() {
            window.location.reload();
        },
        handleOutsideClick(event) {
            const dropdown = this.$el.querySelector('.dropdown');
            const toggleBtn = this.$el.querySelector('span[active-class="active"]');
            
            if (dropdown && !dropdown.contains(event.target) && !toggleBtn.contains(event.target)) {
                this.isDropdownOpen = false;
            }
        },
    },
    mounted() {
        document.addEventListener('click', this.handleOutsideClick);
    },
    beforeDestroy() {
        document.removeEventListener('click', this.handleOutsideClick);
    }
}
</script>

<style scoped>
    .header{
        position: relative;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 70px;
        background-color:#292c2f;
        color: #ffffff;

        /*吸顶效果*/
        position: sticky;
        position: -webkit-sticky;    
        /* !*兼容 -webkit 内核的浏览器*! */
        top: 0px;                    
        /* !*必须设一个值，否则不生效*! */

        /* border: 1px solid black; */

    }
    .box{
        display: flex;
        align-items: center;
    }
    h1{
        display: flex;
        align-items: center;
        font: normal 28px Cookie, Arial, Helvetica, sans-serif;
        padding: 0px 20px;
    }
    img{
        width: 70px;
        height: 40px;
    }

    nav {
        display: flex;
        align-items: center;
        margin: 0px 40px;
        font:16px Arial, Helvetica, sans-serif;
    }
    nav a{
        padding: 0 15px;
        width: 70px;
        text-decoration:none;
        color: #ffffff;
        font-size: 16px;
        font-weight: normal;
        opacity: 0.9;
    }

    nav a:hover {
        opacity: 1;
    }

    .active {
        color: #608bd2;
        pointer-events: none;
        opacity: 1;
    }

    /*搜索框*/

    .text{
        height: 22px;
        font-size: 14px;
        border: 1px solid #ccc;
        padding: 3px 16px;
        border-bottom-left-radius: 20px;
        border-top-left-radius: 20px;
    }
    .text:focus{
        outline: none;
        border-color: rgba(82, 168, 236, 0.8);
        box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
    }
    .button{
        width: 60px;
        height: 30px;
        font-size: 14px;
        margin-right: 35px;
        border: 1px solid #608bd2;
        background-color: #608bd2;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
    }

    .contents{
        /* display: flex; */
        /* justify-content: center; */
        /* border: 1px solid red; */
        height: 100vh;

    }
    .content{
    border: 1px solid red;

        /* display: flex; */
        /* width: 1400px;
        height: 1400px; */
        /* background-color: #f0f2f3; */
    }
    .language-dropdown-wrapper {
        position: relative;
    }
    .dropdown {
        position: absolute;
        top: 100%;      /* 定位在“选择语言”标签的正下方 */
        left: 0;        /* 与“选择语言”左侧对齐 */
        background-color: #292c2f;
        border: 1px solid #608bd2;
        border-radius: 5px;
        width: 100px;
        z-index: 3; /* 确保下拉框在其他元素上方 */
    }

    .dropdown-item {
        display: flex;
        align-items: center;
        padding: 5px 10px;
        /* color: #ccc; */
    }

    .dropdown-item input[type="radio"] {
        margin-right: 10px;
    }
    .language-dropdown-wrapper span:hover {
    cursor: pointer;  /* 鼠标样式变为手指 */
     color: #ffffff;   
}
.language-dropdown-wrapper span{
    color: #eee; 
}


</style>
