<template>
    <div class="box">
        <div class="main">
                <div class="left">
                <img src="../assets/bg.png" alt="logo" class="logo">
            </div>
            <div class="right">
                <h1>{{this.$store.state.language[23] }}
                    <br>
                    {{this.$store.state.language[24] }}
                </h1>
                <p>
                    <nav>
                    <router-link active-class="active" to="/execute"> {{this.$store.state.language[25] }} </router-link>
                    <router-link active-class="active" to="/algorithm"> {{this.$store.state.language[26] }} </router-link>
                    </nav>
                    <!-- <a href="" @click="test()"><span>{{this.$store.state.language[25] }}</span></a> -->
                    <!-- <a href="" @click="about()"><span>{{this.$store.state.language[26] }}</span></a> -->
                </p>
            </div>
        </div>

        <div class="button">
            <div class="point" :style="pointStyle">
                <p>{{this.$store.state.language[27] }}</p>
                <h2>{{this.$store.state.language[28] }}<br>
                    {{this.$store.state.language[29] }}</h2>
            </div>
            <div class="point">
                <p>{{this.$store.state.language[30] }}</p>
                <h2>{{this.$store.state.language[31] }}<br>
                    {{this.$store.state.language[32] }}
                </h2>
            </div>
            <div class="point">
                <p>{{this.$store.state.language[33] }}</p>
                <h2>{{this.$store.state.language[34] }}<br>
                    {{this.$store.state.language[35] }}
                </h2>
            </div>
        </div>
        <a href="" target="_blank" class="web_number" v-if="isAtBottomOrNoOverflow">鲁ICP备2022030866号-1</a>
    </div>
</template>

<script>
export default {
    name: 'Intro',

    data() {
        return {
            isAtBottomOrNoOverflow: true 
            
        };
    },
    // mounted() {
    //     window.addEventListener('scroll', this.checkVisibility);
    //     this.checkVisibility(); // 页面加载时检查
    // },

    // beforeDestroy() {
    //     window.removeEventListener('scroll', this.checkVisibility);
    // },
    mounted() {
        window.addEventListener('scroll', this.handleScroll);
    },

    beforeDestroy() {
        window.removeEventListener('scroll', this.handleScroll);
    },
    computed:{
        pointStyle() {
            if (this.$store.state.language_state == 1) {
                return { marginLeft: '0' };
            } else if (this.$store.state.language_state == 2) {
                return { marginLeft: '0' };
            }
            return {}; // 默认情况或者其他情况
            },
    },

    

    methods: {
        checkVisibility() {
            // 检查是否滚动到了页面底部
            const scrolledToBottom = window.innerHeight + window.scrollY >= document.documentElement.scrollHeight;

            // 检查页面内容是否溢出
            const contentNotOverflowing = document.documentElement.scrollHeight <= window.innerHeight;

            // 如果满足以上任一条件
            if (scrolledToBottom || contentNotOverflowing) {
                this.isAtBottomOrNoOverflow = true;
            } else {
                this.isAtBottomOrNoOverflow = false;
            }
        },
        test(){
            this.$router.push({
                name:'execute'
            })
        },
        about(){
            this.$router.push({
                name:'algorithm'
            })
        }
    },
};
</script>

